.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1040;
  backdrop-filter: blur(4px);
  transition: opacity 0.4s ease-in-out;
  opacity: 1;
  padding: 0.5rem;
}

.modal {
  background: #ffffff;
  border-radius: 0.5rem;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 10px 20px;
  width: 90%;
  max-width: 600px;
  padding: 2rem;
  position: relative;
  transform: translateY(0);
  opacity: 1;
  transition: transform 0.4s ease-in-out, opacity 0.4s ease-in-out;
}
.modal--sm {
  max-width: 400px;
}
.modal--md {
  max-width: 600px;
}
.modal--lg {
  max-width: 800px;
}

.modal.hidden {
  opacity: 0;
  transform: translateY(-10px);
  pointer-events: none;
}

.modal__header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #d8dfe7;
  padding-bottom: 0.5rem;
  margin-bottom: 1.5rem;
}

.modal__title {
  font-size: 1.75rem;
  font-weight: 600;
  color: #313b5e;
  flex-grow: 1;
  font-family: "Nunito", sans-serif;
}

.modal__close {
  background: none;
  border: none;
  font-size: 1.75rem;
  cursor: pointer;
  color: #6c757d;
  padding: 0.25rem;
  border-radius: 0.2rem;
  transition: color 0.2s ease-in-out, background 0.2s ease-in-out;
}
.modal__close:hover {
  color: #ffffff;
  background: #6c757d;
}

.modal__content {
  font-size: 1rem;
  color: #6e708c;
  max-height: 60vh;
  overflow-y: auto;
  padding-right: 0.5rem;
}

@media (max-width: 768px) {
  .modal {
    width: 95%;
    max-width: 90%;
    padding: 1.5rem;
  }
}
